import React from 'react';
import ProLayout from '@ant-design/pro-layout';
import defaultProps from './BasicDefault';
import { Route, Link } from 'react-router-dom';
import RightContent from '../../components/rightContent';
import Loadable from 'react-loadable';
import UserOutlined from '@ant-design/icons';
import { Avatar } from 'antd';

export default class Basiclayouts extends React.Component {


  // constructor(props) {
  //   super(props);
  // }

  render() {

    const {
      location = {
        pathname: '/',
      },
    } = this.props;



    const getRoutesList = (routes) => {
      let arr = [];
      //console.log(routes) 
      routes.map((item) => {
        if (item.routes) {
          arr.push(...getRoutesList(item.routes));
        } else {
          arr.push(
            <Route path={item.path} component={Loadable({
              loader: () => import(`../../pages/${item.component}`),
              loading: () => <div>正在加载！</div>,
            })} />
          )
        }
        return null;
      })
      console.log(arr)
      return arr;
    }

    return (
      <div
        style={{
          height: '100vh',
        }}
      >
        <ProLayout
          menuItemRender={(menuItemProps, defaultDom) => {
            //{console.log(menuItemProps)}
            if (
              menuItemProps.isUrl ||
              !menuItemProps.path ||
              location.pathname === menuItemProps.path
            ) {
              return defaultDom;
            }
            return <Link to={menuItemProps.path}>{defaultDom}</Link>;
          }}
          //subMenuItemRender={(_, dom) => <div>{dom}{console.log(_)}</div>}
          title="正阳油漆"
          logo="https://gw.alipayobjects.com/mdn/rms_b5fcc5/afts/img/A*1NHAQYduQiQAAAAAAAAAAABkARQnAQ"
          menuHeaderRender={(logo, title) => (
            <div
              id="customize_me nu_header"
              onClick={() => {
                // window.open('');
              }}
            >
              {logo}
              {title}
            </div>
          )}
          rightContentRender={() => (
            <div>
              <Avatar shape="square" size="small" icon={<UserOutlined />} />
            </div>
          )}
          {...defaultProps}
        // layout='top'
        //navTheme={'dark'}
        // breadcrumbRender={(route)=>{
        //   console.log(route)
        // }}
        >
          <RightContent routes={getRoutesList(defaultProps.route.routes)} />
        </ProLayout>
      </div>
    );
  }
};