import React from 'react';
import { UserOutlined, CrownOutlined, ShoppingOutlined } from '@ant-design/icons';
const defaultProps = {
  route: {
    path: '/',
    routes: [
      {
        path: '/dashboard/shopping',
        name: '选购',
        component: 'dashboard/shopping',
        icon: <ShoppingOutlined />,
      },
      {
        path: '/dashboard/account',
        name: '我的账户',
        component: 'dashboard/account',
        icon: <UserOutlined />,
      },
      {
        path: '/dashboard/admin',
        name: '管理员',
        icon: <CrownOutlined />,
        access: 'canAdmin',
        routes: [
          {
            path: '/dashboard/admin/sub-page1',
            name: '一',
            icon: <CrownOutlined />,
            component: 'Welcome',
          },
          {
            path: '/dashboard/admin/sub-page2',
            name: '二',
            icon: <CrownOutlined />,
            component: 'Welcome',
          },
          {
            path: '/dashboard/admin/sub-page3',
            name: '三',
            icon: <CrownOutlined />,
            component: 'Welcome',
          },
        ],
      },
    ],
  },
  location: {
    pathname: '/',
  },
}
export default defaultProps