import React from 'react'
import {  Switch,Redirect } from 'react-router-dom';

export default class RightContent extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            routes: [],
        }
    }

    componentDidMount() {
        const routes = this.props.routes
        this.setState({
            routes: routes,
        })
    }



    render() {
        return (
            <>
                <Switch>
                    {this.state.routes}
                    <Redirect from="/dashboard" to="/dashboard/shopping" />
                </Switch>
            </>
        )
    }
}